

















































































import Logo from "../misc/Logo.vue";
import ProgressCard from "./ProgressCard.vue";
import NumberCard from "./NumberCard.vue";
import ChartCard from "./ChartCard.vue";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { settingsModule } from "@/store/modules";

@Component({
  components: {
    ChartCard,
    ProgressCard,
    Logo,
    NumberCard
  }
})
export default class HomeOverviewClass extends Mixins() {
  interval: number = 0;
  value: number = 0;

  beforeDestroy() {
    clearTimeout(this.interval);
  }

  mounted() {
    this.interval = setTimeout(this.nextTimeout, 100);
  }

  nextTimeout() {
    if (this.value >= 100) {
      this.value = 0;
    }
    this.value += Math.floor(Math.random() * 10);
    this.value = Math.min(100, this.value);
    this.interval = setTimeout(this.nextTimeout, Math.random() * 1000 + 500);
  }

  get greeting() {
    return this.$t("home.greeting", {
      name: settingsModule.fullUserName
    });
  }

  get chartData() {
    return {
      labels: [
        this.$t("month.january"),
        this.$t("month.february"),
        this.$t("month.march"),
        this.$t("month.april"),
        this.$t("month.may"),
        this.$t("month.june"),
        this.$t("month.july"),
        this.$t("month.august"),
        this.$t("month.september"),
        this.$t("month.october"),
        this.$t("month.november"),
        this.$t("month.december")
      ],
      series: [[54, 44, 32, 78, 55, 45, 32, 43, 56, 61, 75, 89]]
    };
  }
}
