<template>
  <settings-section :title="$t('setting.email-and-notifications.title')">
    <settings-item
      :value="sendEmails"
      @input="setSendEmails"
      type="switch"
      :title="$t('setting.send-emails.list-title')"
      :description="$t('setting.send-emails.list-description')"
      show-action
    ></settings-item>
    <settings-item
      v-model="reportLanguageDialog"
      :title="$t('setting.report-language.list-title')"
      :description="$t('setting.report-language.list-description')"
    >
      <settings-dialog
        :title="$t('setting.report-language.dialog-title')"
        v-model="reportLanguageDialog"
        scrollable
      >
        <settings-dialog-list
          :list="availableReportLanguages"
          @click="setReportLanguage($event.value); reportLanguageDialog = false;"
          showAvatar
        />
      </settings-dialog>
    </settings-item>

        <settings-item
      v-model="smtpDialog"
      :title="$t('setting.smtp.list-title')"
      :description="$t('setting.smtp.list-description')"
    >
      <settings-dialog
        :title="$t('setting.smtp.dialog-title')"
        v-model="smtpDialog"
        show-save
        @input-save="saveSmtpSettings"
        @cancel="resetSmtpSettings"
      >
        <v-text-field
          v-model="currentSmtpHostname"
          box
          :label="$t('setting.smtp.dialog.hostname')"
        />
        <v-text-field
          v-model="currentSmtpPort"
          type="number"
          box
          :label="$t('setting.smtp.dialog.port')"
        />
        <v-text-field v-model="currentSmtpFrom" box :label="$t('setting.smtp.dialog.from')"/>
        <v-text-field
          v-model="currentSmtpPassword"
          box
          :label="$t('setting.smtp.dialog.password')"
        />
        <v-text-field
          v-model="currentSmtpUsername"
          box
          :label="$t('setting.smtp.dialog.username')"
        />
        <recipient-input
          v-model="currentSmtpRecipients"
          :label="$t('setting.smtp.dialog.recipients')"
        />
      </settings-dialog>
    </settings-item>

    <settings-item
      value
      @input="testSmtpConnection"
      :title="$t('setting.smtp.test-connection')"
      :description="$t('setting.smtp.test-connection')"
    ></settings-item>
  </settings-section>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialogList from "./SettingsDialogList";
import SettingsDialog from "./SettingsDialog";
import RecipientInput from "./RecipientInput";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog,
    RecipientInput
  },
  data() {
    return {
      smtpDialog: false,
      smtpPortDialog: false,
      reportLanguageDialog: false,

      currentSmtpHostname: "",
      currentSmtpPort: 0,
      currentSmtpFrom: "",
      currentSmtpPassword: "",
      currentSmtpUsername: "",
      currentSmtpRecipients: []
    };
  },
  computed: {
    ...mapGetters({
      sendEmails: "settings/sendEmails",
      reportLanguage: "settings/reportLanguage",
      smtpHostname: "settings/smtpHostname",
      smtpPort: "settings/smtpPort",
      smtpFrom: "settings/smtpFrom",
      smtpPassword: "settings/smtpPassword",
      smtpUsername: "settings/smtpUsername",
      smtpRecipients: "settings/smtpRecipients"
    }),
    availableReportLanguages() {
      return [
        {
          value: "gui",
          avatar: "G",
          title: this.$t("language.same-as-gui"),
          active: this.reportLanguage === "gui"
        },
        {
          value: "de",
          avatar: "DE",
          title: this.$t("language.german"),
          active: this.reportLanguage === "de"
        },
        {
          value: "en",
          avatar: "EN",
          title: this.$t("language.english"),
          active: this.reportLanguage === "en"
        }
      ];
    }
  },
  methods: {
    ...mapMutations({
      setSendEmails: "settings/setSendEmails",
      setReportLanguage: "settings/setReportLanguage",
      setSmtpHostname: "settings/setSmtpHostname",
      setSmtpPort: "settings/setSmtpPort",
      setSmtpFrom: "settings/setSmtpFrom",
      setSmtpPassword: "settings/setSmtpPassword",
      setSmtpUsername: "settings/setSmtpUsername",
      setSmtpRecipients: "settings/setSmtpRecipients"
    }),
    saveSmtpSettings() {
      this.setSmtpHostname(this.currentSmtpHostname);
      this.setSmtpPort(Number(this.currentSmtpPort));
      this.setSmtpFrom(this.currentSmtpFrom);
      this.setSmtpPassword(this.currentSmtpPassword);
      this.setSmtpUsername(this.currentSmtpUsername);
      this.setSmtpRecipients([this.currentSmtpRecipients]); // TODO
    },
    resetSmtpSettings() {
      this.currentSmtpHostname = this.smtpHostname;
      this.currentSmtpPort = this.smtpPort;
      this.currentSmtpFrom = this.smtpFrom;
      this.currentSmtpPassword = this.smtpPassword;
      this.currentSmtpUsername = this.smtpUsername;
      this.currentSmtpRecipients = this.smtpRecipients;
    },
    testSmtpConnection() {
      alert("test connection not implemented");
    }
  },
  watch: {
    smtpHostname: {
      immediate: true,
      handler(val) {
        this.currentSmtpHostname = val;
      }
    },
    smtpPort: {
      immediate: true,
      handler(val) {
        this.currentSmtpPort = val;
      }
    },
    smtpFrom: {
      immediate: true,
      handler(val) {
        this.currentSmtpFrom = val;
      }
    },
    smtpPassword: {
      immediate: true,
      handler(val) {
        this.currentSmtpPassword = val;
      }
    },
    smtpUsername: {
      immediate: true,
      handler(val) {
        this.currentSmtpUsername = val;
      }
    },
    smtpRecipients: {
      immediate: true,
      handler(val) {
        this.currentSmtpRecipients = val;
      }
    }
  }
};
</script>
