








































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Emit,
  Watch
} from "vue-property-decorator";
import { LocaleMessages } from "vue-i18n";
import { repositoriesModule, routerModule } from "@/store/modules";
import { Overwrite } from "@/shared/types";
import { IJob } from "@/store/modules/JobsModule";
import RepeatPatternInput from "@/components/misc/RepeatPatternInput.vue";

type JobOptionalId = Overwrite<
  IJob,
  {
    id?: number;
  }
>;

@Component({
  components: {
    RepeatPatternInput
  }
})
export default class RepositoryEditorComponent extends Mixins() {
  maxNameLength = 100;
  valid = true;
  name = "";
  repoId: number | null = null;
  maxBackups: number | string = 100;
  emailNotification = "none"; // TODO: types
  backupLocations: string[] = []; // TODO: add editor
  repeatPattern = "* * * * * *";

  @Prop({
    type: Object,
    default: () =>
      ({
        name: "",
        repoId: 0,
        maxBackups: 100,
        emailNotification: "none",
        backupLocations: [],
        repeatPattern: "* * * * * *"
      } as JobOptionalId)
  })
  initialData!: JobOptionalId;

  @Watch("initialData", { immediate: true })
  updateLocalData() {
    this.name = this.initialData.name;
    this.repoId =
      this.initialData.repoId || Number(routerModule.query.repository) || null; // TODO: maybe update types
    this.maxBackups = this.initialData.maxBackups;
    this.emailNotification = this.initialData.emailNotification;
    this.backupLocations = [...this.initialData.backupLocations];
    this.repeatPattern = this.initialData.repeatPattern;
  }

  get availableRepositories() {
    return repositoriesModule.allRepositories.map(r => ({
      text: r.name,
      value: r.id
    }));
  }

  get availableEmailNotificationItems() {
    // TODO: enum
    return [
      { text: this.$t("notification-settings.never"), value: "never" },
      { text: this.$t("notification-settings.on-error"), value: "onError" },
      { text: this.$t("notification-settings.always"), value: "always" }
    ];
  }

  async submit() {
    if ((this.$refs.form as any).validate()) {
      this.formSubmit({
        name: this.name,
        repoId: Number(this.repoId),
        maxBackups: Number(this.maxBackups),
        emailNotification: this.emailNotification,
        backupLocations: this.backupLocations,
        repeatPattern: this.repeatPattern,
        id: this.initialData.id
      });
    }
  }

  @Emit("form-submit")
  formSubmit(payload: JobOptionalId) {}

  async clear() {
    (this.$refs.form as any).reset(); // TODO: fix types
    this.$nextTick(() => {
      this.updateLocalData();
    });
  }

  get validateRules(): {
    [idx: string]: ((val: string) => boolean | string | LocaleMessages)[];
  } {
    return {
      repoId: [v => !!v || this.$t("job-editor.repository-required")],
      maxBackups: [
        v => !!v || this.$t("job-editor.max-number-of-backups-required")
      ],
      emailNotification: [
        v => !!v || this.$t("job-editor.email-notification-settings-required")
      ],
      name: [
        v => !!v || this.$t("job-editor.name-required"),
        v =>
          (v && v.length <= this.maxNameLength) ||
          this.$t("job-editor.name-length", {
            length: this.maxNameLength
          })
      ]
    };
  }
}
