









import { Vue, Component, Prop, Mixins } from "vue-property-decorator";

@Component({})
export default class ThemeItem extends Mixins() {
  @Prop({
    type: String,
    default: "Section title not set"
  })
  title!: string;
  @Prop({
    type: Boolean,
    default: false
  })
  important!: boolean;
}
