<template>
  <settings-section :title="$t('setting.user.title')">
    <settings-item
      v-model="usernameDialog"
      :title="$t('setting.username.list-title')"
      :description="$t('setting.username.list-description')"
    >
      <settings-dialog
        :title="$t('setting.username.dialog-title')"
        v-model="usernameDialog"
        show-save
        preset="textField"
        :input="username"
        @input-save="setUsername"
      />
    </settings-item>
    <settings-item
      v-model="firstNameDialog"
      :title="$t('setting.first-name.list-title')"
      :description="$t('setting.first-name.list-description')"
    >
      <settings-dialog
        :title="$t('setting.first-name.dialog-title')"
        v-model="firstNameDialog"
        show-save
        preset="textField"
        :input="firstName"
        @input-save="setFirstName"
      />
    </settings-item>
    <settings-item
      v-model="lastNameDialog"
      :title="$t('setting.last-name.list-title')"
      :description="$t('setting.last-name.list-description')"
    >
      <settings-dialog
        :title="$t('setting.last-name.dialog-title')"
        v-model="lastNameDialog"
        show-save
        preset="textField"
        :input="lastName"
        @input-save="setLastName"
      />
    </settings-item>
    <settings-item
      v-model="emailDialog"
      :title="$t('setting.email.list-title')"
      :description="$t('setting.email.list-description')"
    >
      <settings-dialog
        :title="$t('setting.email.dialog-title')"
        v-model="emailDialog"
        show-save
        preset="textField"
        :input="email"
        @input-save="setEmail"
      />
    </settings-item>
  </settings-section>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialogList from "./SettingsDialogList";
import SettingsDialog from "./SettingsDialog";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  },
  data() {
    return {
      usernameDialog: false,
      firstNameDialog: false,
      emailDialog: false,
      lastNameDialog: false
    };
  },
  computed: {
    ...mapGetters({
      username: "settings/username",
      firstName: "settings/firstName",
      email: "settings/email",
      lastName: "settings/lastName"
    })
  },
  methods: {
    ...mapMutations({
      setUsername: "settings/setUsername",
      setFirstName: "settings/setFirstName",
      setEmail: "settings/setEmail",
      setLastName: "settings/setLastName"
    })
  }
};
</script>
