<template>
  <settings-section :title="$t('setting.gui-settings.title')">
    <settings-item
      type="other"
      v-model="themeDialog"
      :title="$t('setting.theme.list-title')"
      :description="$t('setting.theme.list-description')"
    >
      <settings-dialog :title="$t('setting.theme.dialog-title')" v-model="themeDialog" scrollable>
        <theme-item
          v-for="(t, idx) in availableThemes"
          :key="idx"
          :primary-color="t.primary"
          :secondary-color="t.secondary"
          :accent-color="t.accent"
          class="my-3"
          @click.native="setTheme(t); themeDialog = false;"
        />
      </settings-dialog>
    </settings-item>
    <settings-item
      :value="theme.dark"
      @input="setDark"
      type="switch"
      :title="$t('setting.dark.list-title')"
      :description="$t('setting.dark.list-description')"
      show-action
    ></settings-item>
    <settings-item
      v-model="languageDialog"
      :title="$t('setting.language.list-title')"
      :description="$t('setting.language.list-description')"
    >
      <settings-dialog
        :title="$t('setting.language.dialog-title')"
        v-model="languageDialog"
        scrollable
      >
        <settings-dialog-list
          :list="availableLanguages"
          @click="setLanguage($event.value); languageDialog = false;"
          showAvatar
        />
      </settings-dialog>
    </settings-item>
    <settings-item
      :value="showSnackbar"
      @input="setShowSnackbar"
      type="switch"
      :title="$t('setting.show-snackbar.list-title')"
      :description="$t('setting.show-snackbar.list-description')"
      show-action
    ></settings-item>
  </settings-section>
</template>

<script>
import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialog from "./SettingsDialog";
import ThemeItem from "./ThemeItem";
import { mapMutations, mapGetters } from "vuex";
import colors from "vuetify/es5/util/colors";
import SettingsDialogList from "./SettingsDialogList";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    ThemeItem,
    SettingsDialog,
    SettingsDialogList
  },
  data() {
    return {
      themeDialog: false,
      languageDialog: false,
      availableThemes: [
        {
          primary: "#456CB2",
          accent: colors.green.base,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.deepOrange.base,
          accent: colors.orange.darken1,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.pink.base,
          accent: colors.lightBlue.base,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.lightGreen.base,
          accent: colors.deepOrange.base,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.blueGrey.base,
          accent: "#456CB2",
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.yellow.darken3,
          accent: colors.pink.lighten3,
          secondary: colors.blueGrey.base
        }
      ]
    };
  },
  computed: {
    ...mapGetters({
      theme: "settings/theme",
      showSnackbar: "settings/showSnackbar",
      currentLanguage: "settings/language"
    }),
    availableLanguages() {
      return [
        {
          value: "de",
          avatar: "DE",
          title: this.$t("language.german"),
          active: this.currentLanguage === "de"
        },
        {
          value: "en",
          avatar: "EN",
          title: this.$t("language.english"),
          active: this.currentLanguage === "en"
        }
      ];
    }
  },
  methods: {
    ...mapMutations({
      setTheme: "settings/setTheme",
      setDark: "settings/setDark",
      setLanguage: "settings/setLanguage",
      setShowSnackbar: "settings/setShowSnackbar"
    })
  }
};
</script>
