











import { Vue, Component, Prop, Mixins, Emit } from "vue-property-decorator";
import parser from "cron-parser";
import cronstrue from "cronstrue/i18n";
import { settingsModule } from "@/store/modules";
import { distanceInWordsStrict } from "date-fns";

import enLocale from "date-fns/locale/en";
import deLocale from "date-fns/locale/de";

(window as any).parser = parser;

// Format:
// *  *  *  *  *  *
// ┬  ┬  ┬  ┬  ┬  ┬
// │  │  │  │  │  │
// │  │  │  │  │  └ day of week (0 - 7) (0 or 7 is Sun)
// │  │  │  │  └─── month (1 - 12)
// │  │  │  └────── day of month (1 - 31)
// │  │  └───────── hour (0 - 23)
// │  └──────────── minute (0 - 59)
// └─────────────── second (0 - 59, optional)

@Component({
  components: {}
})
export default class RepeatPatternInput extends Mixins() {
  @Prop({})
  value?: string;

  @Prop({
    type: String,
    default: ""
  })
  label!: string;

  @Emit("input")
  setValue(val: string) {}

  set cronExpression(val: string) {
    this.setValue(val);
  }

  get cronExpression() {
    return (this.value || "* * * * * *")
      .split(/\s/g)
      .map(s => s.trim())
      .filter(s => s && s.length)
      .join(" ");
  }

  get nextExecutionDates() {
    const interval = parser.parseExpression(this.cronExpression);
    return new Array(10)
      .fill(null)
      .map(() =>
        distanceInWordsStrict(interval.next().toDate(), new Date(), {
          locale:
            settingsModule.language === "de" // todo: enum
              ? deLocale
              : enLocale
        })
      )
      .map(s => s.trim())
      .join(", ");
  }

  get humanReadableExpression() {
    try {
      return cronstrue.toString(this.cronExpression, {
        use24HourTimeFormat: true,
        locale: settingsModule.language
      });
    } catch (error) {}
    return this.$t("scheduling.cron.invalid-expression");
  }
}
