


























import { Vue, Component, Prop, Mixins } from "vue-property-decorator";

@Component({
  inheritAttrs: false
})
export default class ChartCardComponent extends Mixins() {
  @Prop({
    type: Array,
    default: () => [
      [
        "screen and (max-width: 640px)",
        {
          seriesBarDistance: 5,
          axisX: {
            labelInterpolationFnc: (value: any) => {
              return value[0];
            }
          }
        }
      ]
    ]
  })
  responsiveOptions: any;

  @Prop({
    default: "Bar",
    type: String,
    validator: v => ["Bar", "Line", "Pie"].includes(v)
  })
  type!: string;

  @Prop({
    default: "",
    type: String
  })
  subTitle!: string;
  @Prop({
    default: "",
    type: String
  })
  heading!: string;
  @Prop({
    default: "",
    type: String
  })
  icon!: string;
  @Prop({
    default: "ct-double-octave",
    type: String
  })
  ratio!: string;

  @Prop({
    default: () => ({}),
    type: Object
  })
  data: any;

  @Prop({
    default: () => [],
    type: Array
  })
  eventHandlers!: Function[];

  @Prop({
    type: Object,
    default: () => ({
      axisX: {
        showGrid: false
      },
      axisY: {
        showGrid: false
      },
      // low: 0,
      // high: 1000,
      chartPadding: {
        top: 24,
        right: 0,
        bottom: 0,
        left: 0
      }
    })
  })
  options: any;
}
