  current!: number | string;
  @Prop({
    type: String,
    default: ""
  })
  to!: string;
  @Prop({
    type: String,
    default: ""
  })
  unit!: string;
  @Prop({
    type: String,
    default: ""
  })
  subTitle!: string;
  @Prop({
    type: String,
    default: ""
  })
  icon!: string;
  @Prop({
    type: String,
    default: ""
  })
