<template>
  <v-dialog v-model="vModel" :max-width="isWide ? '600px' : '400px'" :scrollable="scrollable">
    <v-card>
      <v-card-title>
        <span class="headline">{{title}}</span>
      </v-card-title>
      <v-card-text>
        <slot>
          <v-text-field
            v-if="preset === 'textField' || preset === 'numberField'"
            box
            :type="preset === 'textField' ? 'text' : 'number'"
            solo
            autofocus
            v-model="currentInputValue"
          />
        </slot>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="accent"
          flat
          @click.native="$emit('cancel'); vModel = false"
        >{{$t("common.interaction.close")}}</v-btn>
        <v-btn
          v-if="showSave"
          color="accent"
          flat
          @click.native="vModel = false; $emit('input-save', preset === 'numberField' ? Number(currentInputValue) : currentInputValue)"
        >{{$t("common.interaction.save")}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    scrollable: {
      type: Boolean,
      default: false
    },
    value: {
      required: true
    },
    showSave: {
      type: Boolean,
      default: false
    },
    preset: {
      // "default"|"textField"|"numberField"
      type: String,
      default: "default"
    },
    input: {
      type: [String, Number],
      default: ""
    }
  },
  data() {
    return {
      currentInputValue: ""
    };
  },
  watch: {
    input: {
      immediate: true,
      handler(val) {
        this.currentInputValue = val;
      }
    },
    vModel() {
      // TODO: emit cancel when user clicks outside
      this.currentInputValue = this.input;
    }
  },
  computed: {
    isWide() {
      return this.preset !== "default";
    },
    vModel: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      }
    }
  }
};
</script>
