<template>
  <settings-section :title="$t('setting.backup.title')">
    <settings-item
      v-model="defaultEmailNotificationDialog"
      :title="$t('setting.default-email-notification-type.list-title')"
      :description="$t('setting.default-email-notification-type.list-description')"
    >
      <settings-dialog
        :title="$t('setting.default-email-notification-type.dialog-title')"
        v-model="defaultEmailNotificationDialog"
      >
        <settings-dialog-list
          :list="defaultEmailNotifications"
          @click="setDefaultEmailNotification($event.value); defaultEmailNotificationDialog = false; "
        />
      </settings-dialog>
    </settings-item>
    <settings-item
      v-model="defaultMaxBackupsPerRepoDialog"
      :title="$t('setting.default-max-number-of-backups-per-repo.list-title')"
      :description="$t('setting.default-max-number-of-backups-per-repo.list-description')"
    >
      <settings-dialog
        :title="$t('setting.default-max-number-of-backups-per-repo.dialog-title')"
        v-model="defaultMaxBackupsPerRepoDialog"
        show-save
        preset="numberField"
        :input="defaultMaxBackupsPerRepo"
        @input-save="setDefaultMaxBackupsPerRepo"
      />
    </settings-item>
    <settings-item
      v-model="defaultUnlockTimeDialog"
      :title="$t('setting.default-unlock-time.list-title')"
      :description="$t('setting.default-unlock-time.list-description')"
    >
      <settings-dialog
        :title="$t('setting.default-unlock-time.dialog-title')"
        v-model="defaultUnlockTimeDialog"
        show-save
        preset="numberField"
        :input="defaultUnlockTime"
        @input-save="setDefaultUnlockTime"
      />
    </settings-item>
  </settings-section>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialogList from "./SettingsDialogList";
import SettingsDialog from "./SettingsDialog";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  },
  data() {
    return {
      defaultEmailNotificationDialog: false,
      defaultMaxBackupsPerRepoDialog: false,
      defaultUnlockTimeDialog: false
    };
  },
  computed: {
    ...mapGetters({
      defaultEmailNotification: "settings/defaultEmailNotification",
      defaultMaxBackupsPerRepo: "settings/defaultMaxBackupsPerRepo",
      defaultUnlockTime: "settings/defaultUnlockTime"
    }),
    defaultEmailNotifications() {
      return [
        {
          value: "ALWAYS",
          title: this.$t("setting.default-email-notification-type.always"),
          active: this.defaultEmailNotification === "ALWAYS"
        },
        {
          value: "ON_ERROR",
          title: this.$t("setting.default-email-notification-type.on-error"),
          active: this.defaultEmailNotification === "ON_ERROR"
        },
        {
          value: "NEVER",
          title: this.$t("setting.default-email-notification-type.never"),
          active: this.defaultEmailNotification === "NEVER"
        }
      ];
    }
  },
  methods: {
    ...mapMutations({
      setDefaultEmailNotification: "settings/setDefaultEmailNotification",
      setDefaultMaxBackupsPerRepo: "settings/setDefaultMaxBackupsPerRepo",
      setDefaultUnlockTime: "settings/setDefaultUnlockTime"
    })
  }
};
</script>
