import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";
import * as api from "../api";
import { Overwrite } from "@/shared/types";

@Module({ dynamic: true, store, name: "jobs", namespaced: true })
export default class SettingsModule extends VuexModule {
  private jobs: IJob[] = [];

  @Action
  async fetchAllJobs() {
    const response = await api.getJobs();
    if (response.payload) {
      this.setJobs(response.payload.jobs);
    }
  }

  @Action
  async createJob(
    job: Overwrite<
      IJob,
      {
        id?: IJob["id"];
      }
    > // TODO: maybe make more fields optional
  ) {
    // TODO: replace with api call
    const resp = await api.createJob(job);
    if (resp.payload) {
      this.setJobs([...this.allJobs, resp.payload]);
      return resp;
    }
  }

  @Action
  async updateJob(job: IJob) {
    // TODO: replace with api call
    const resp = await api.updateJob(job);
    if (resp.payload) {
      this.setJobs(this.allJobs.map(j => (j.id === job.id ? job : j)));
      return resp;
    }
  }

  @Action
  async deleteJob(id: number) {
    // TODO: replace with api call
    const resp = await api.deleteJob(id);
    const newJobs = this.allJobs.filter(r => r.id !== id);
    this.setJobs(newJobs);
    return resp;
  }

  @Mutation
  setJobs(jobs: IJob[]) {
    this.jobs = jobs;
  }

  get recentlyUsed() {
    // return at most 5 jobs (shown in side menu)
    return this.jobs.slice(0, 5);
  }
  get allJobs() {
    return this.jobs;
  }

  get getById() {
    return (id: string | number) => {
      const num = Number(id);
      const job = this.allJobs.find(r => Number(r.id) === num);
      return job;
    };
  }

  get getByRepository() {
    return (repoId: string | number) => {
      const num = Number(repoId);
      return this.allJobs.filter(j => j.repoId === num);
    };
  }
}

export interface IJob {
  name: string;
  id: number;
  repoId: number;
  maxBackups: number;
  emailNotification: string; // TODO: enum
  backupLocations: string[]; // TODO: change in api
  repeatPattern: string; // TODO: add in api
}

export interface IJobsState {
  jobs: IJob[];
}
