<template>
  <settings-section :title="$t('setting.general.title')">
    <settings-item
      :value="automaticUpdates"
      @input="setAutomaticUpdates"
      type="switch"
      :title="$t('setting.enable-automatic-update.list-title')"
      :description="$t('setting.enable-automatic-update.list-description')"
      show-action
    ></settings-item>
    <settings-item
      :value="enableRegister"
      @input="setEnableRegister"
      type="switch"
      :title="$t('setting.enable-register.list-title')"
      :description="$t('setting.enable-register.list-description')"
      show-action
    ></settings-item>
    <settings-item
      v-model="updateCheckDialog"
      :title="$t('setting.update-check-interval.list-title')"
      :description="$t('setting.update-check-interval.list-description')"
    >
      <settings-dialog
        :title="$t('setting.update-check-interval.dialog-title')"
        v-model="updateCheckDialog"
        scrollable
      >
        <settings-dialog-list
          :list="availableUpdateIntervals"
          @click="setUpdateCheckInterval($event.value); updateCheckDialog = false; "
        />
      </settings-dialog>
    </settings-item>
  </settings-section>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialogList from "./SettingsDialogList";
import SettingsDialog from "./SettingsDialog";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  },
  data() {
    return {
      updateCheckDialog: false
    };
  },
  computed: {
    ...mapGetters({
      automaticUpdates: "settings/automaticUpdates",
      updateCheckInterval: "settings/updateCheckInterval",
      enableRegister: "settings/enableRegister"
    }),
    availableUpdateIntervals() {
      return [
        {
          value: "HOURLY",
          avatar: this.$t("setting.update-check-interval.hourly")
            .substr(0, 1)
            .toUpperCase(),
          title: this.$t("setting.update-check-interval.hourly"),
          active: this.updateCheckInterval === "HOURLY"
        },
        {
          value: "DAILY",
          avatar: this.$t("setting.update-check-interval.daily")
            .substr(0, 1)
            .toUpperCase(),
          title: this.$t("setting.update-check-interval.daily"),
          active: this.updateCheckInterval === "DAILY"
        },
        {
          value: "WEEKLY",
          avatar: this.$t("setting.update-check-interval.weekly")
            .substr(0, 1)
            .toUpperCase(),
          title: this.$t("setting.update-check-interval.weekly"),
          active: this.updateCheckInterval === "WEEKLY"
        }
      ];
    }
  },
  methods: {
    ...mapMutations({
      setAutomaticUpdates: "settings/setAutomaticUpdates",
      setUpdateCheckInterval: "settings/setUpdateCheckInterval",
      setEnableRegister: "settings/setEnableRegister"
    })
  }
};
</script>
